.matador-screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.matador-deprecated-notice {
  border: 4px solid orange;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #ffc;
  p:last-child {
    margin-bottom: 0;
  }
}

.matador-button,
input[type=submit].matador-button,
input[type=reset].matador-button,
button.matador-button {
  display: inline-block;
  padding: 8px 10px;
  margin: 8px 10px 8px 0;
  color: $primary-foreground;
  background-color: $primary-background;
  border: 0;
  box-shadow: none;
  text-decoration: none;
  cursor: pointer;
  &:focus,
  &:hover {
    background-color: transparentize( $primary-background, .2 );
    border: 0;
    box-shadow: none;
    color: $primary-foreground;
  }
}
.matador-button-secondary,
input[type=submit].matador-button-secondary,
input[type=reset].matador-button-secondary,
button.matador-button-secondary {
  color: $secondary-foreground;
  background-color: $secondary-background;
  &:focus,
  &:hover {
    color: $secondary-foreground;
    background-color: transparentize( $secondary-background, .2 );
  }
}
.matador-button-tertiary,
input[type=submit].matador-button-tertiary,
input[type=reset].matador-button-tertiary,
button.matador-button-tertiary {
  color: $tertiary-foreground;
  background-color: $tertiary-background;
  &:hover {
    color: $tertiary-foreground;
    background-color: transparentize( $tertiary-background, .2 );
  }
}